* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root {
    font-size: 14px;
    font-family: 'Roboto', sans-serif;

    --bg: #EBEBEB;
    --white: #e0dcdc;
    --prog: #e0dcdc;
    ;
}


body {
    background: var(--bg);
    color: var(--black);
}

a {
    text-decoration: none;
}

.player {
    width: 80%;
    max-width: 295px;
    min-height: 538px;
    margin: 20px auto;
    padding: 1rem 1.25rem;
    border-radius: 15px;
    border: 2px solid #2a384a;
    position: relative;
    background-color: #3d4a5b;

}

.player::before {
    content: '';
    display: block;
    background: #d3d3d3;
    width: 100px;
    height: 20px;
    border-radius: 0 0 50% 50%;
    position: absolute;
    top: 0;
    left: 87px;
}

.header {
    height: 40px;
    display: grid;
    grid-template-columns: 1fr 3fr 1fr;
    align-items: center;
    justify-items: center;
}

.header>.button {
    width: 2rem;
    height: 2rem;
    display: grid;
    align-items: center;
    justify-items: center;
    color: var(--black);
    background: linear-gradient(145deg, #3d4a5b, #3b4859);
    box-shadow: 4px 4px 7px #566b86, -4px -4px 7px #53647a;
    border-radius: 6px;
}

.art {
    display: block;
    width: 195px;
    height: 195px;
    margin: 1rem auto;
    /* border-radius: 50%; */

    border: 4px solid #404066;
    box-shadow: 16px 16px 57px #4c5d73, -16px -16px 57px #3e4756;
}

.info {
    margin: 1rem 0;
    text-align: center;
}

.info h1 {
    color: var(--white);
    font-weight: 500;
    margin-bottom: 0.5rem;
}

.info p {
    font-size: 1.15rem;
    color: var(--white);
}

.prog {
    padding: 2px 0;
}

.prog-time {
    padding: 3px 5px;
    color: var(--white);
    font-weight: bold;
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
}

.prog-time>p {
    font-size: 0.65rem;
}

.prog-bar {
    width: 100%;
    height: 10px;
    padding: 1px;
    border-radius: 25px;

    background: linear-gradient(145deg, #fbfbfb, #d4d4d4);
    background: linear-gradient(145deg, #313e50, #303b4a);
    /* box-shadow: 5px 5px 10px #608795, -5px -5px 10px #455c64; */

}

.prog-bar-inner {
    height: 8px;
    background: var(--white);
    border-radius: 25px;
    width: 100%;
}

.buttons {

    /* margin: 3rem 0; */
    margin-top: 1rem;
    list-style: none;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 3.5rem;
}

.button {
    color: var(--prog);
    border-radius: 50%;
    margin: 0 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 5px 5px 10px #6380a6, -5px -5px 10px #667fa0;
}

.button-sm {
    width: 2rem;
    height: 2rem;
    opacity: 0.75;
}

.button-md {
    width: 2.5rem;
    height: 2.5rem;
    opacity: 0.85;
}

.button-lg {
    width: 3.25rem;
    height: 3.25rem;
    box-shadow: inset 6px 6px 12px #667fa0,
        inset -6px -6px 12px #6380a6
}

i {
    color: var(--white);
}

.bar {
    width: 50%;
    height: 2px;
    margin: auto;
    background: var(--prog);
    border-radius: 25px;
    position: absolute;
    left: 25%;
    bottom: 1rem;
}

.credit {
    background: linear-gradient(45deg, #3297D3, #8FEFFB);
    color: var(--black);
    padding: 1rem 0;
    text-align: center;
}


