@keyframes rolling-disk {
    0% {
        transform: rotate(0);
    }

    100% {
        transform: rotate(1turn);
    }
}

.Music-Player-outer {
    background: linear-gradient(to bottom, #13a476, #1f2937 800px);
    padding: 35px 0px;
    height: 95vh;
    display: flex;
    justify-content: center;
    align-items: center;
}


.Music-Player-outer .player {
    margin: 0px auto;
}

@media screen and (max-width: 1130px) {

    .song-card-sub-sec {
        width: 98vw;
    }
}